import React from "react";
import "./style.css";
import LinkIcon from "./linkedinicon.png"





function LinkProfile() {
  return (
    

    <div className="row bg-info h-100 m-0 p-0 " >
    
      <div id="LinkFeed" className="col-12 col-md-6 offset-md-5 h-100  p-0" style={{ overflowY:"auto",position:"absolute",top:"0",bottom:"0"}}>
      
        <div className="row border-bottom border-secondary">
          <div className="col-12 text-white-50">

            <div className="bgSample row justify-content-center bg-dark mt-0">
              <div className="img-container col-2 p-3" >
                
                <img src={LinkIcon} className="align-self-center" width="100%"  alt="li" />
              </div>
             
            </div>

            <div className="row justify-content-center">
              <h3 className="text-center">Lloyd Barnes </h3>
              
            </div>
            
            <div className="row text-center">
            <div className="col-3"> Profile </div>
              <div className="col-3"> Gallery </div>
              <div className="col-3"> Likes</div>
              <div className="col-3"> Share</div>
            </div>
            
          </div>
        </div>

        <div style={{ height:"auto"}}>
        
        </div>
        <div className="row h-10 bg-dark justify-content-center pt-3 pt-md-1 pb-3 pb-md-1 border-top border-secondary">
          <div className="col-2 col-md-1 p-2 p-md-3 m-0">
            
          </div>

    






          <div className="col-4 col-md-3 text-white-50 m-0 p-0 align-self-center">
            Back to Top
          </div>
        </div>

        
          
          
          

          

      </div>

    </div>

    
  );
}

export default LinkProfile;
 








